import React, { useState } from 'react';
import ReactSelect from 'react-select';
import styles from './SearchTypeSelector.module.scss';

const typesList = [{ searchType: 'query' }, { searchType: 'ingredients' }];

const customStyles = {
  menu: (provided) => ({
    ...provided,
    background: "transparent",
  }),
  option: (provided, state) => ({ 
    ...provided,
    backgroundColor: state.isFocused ? '#D9D9D9' : 'white',
    color: state.isFocused ? 'white' : 'black',
  }),
  input: (provided) => ({
    ...provided,
    color: '#8BAA36',
  }),
  dropdownIndicator: (provided, state) => ({
    ...provided,
    border: 'none',
    boxShadow: 'none',
    color: '#8BAA36', // Ð—Ð¼Ñ–Ð½Ð° ÐºÐ¾Ð»Ñ–Ñ€Ñƒ ÑÑ‚Ñ€Ñ–Ð»ÐºÐ¸ Ð¿Ñ€Ð¸ Ð½Ð°Ð²ÐµÐ´ÐµÐ½Ð½Ñ–
  }),
  control: (provided, state) => ({
    ...provided,
    height: 34,
    width: 143,
    background: "#D9D9D9",
    borderRadius: 6,
    borderColor: state.isFocused ? '#8BAA36' : '#CED4DA', // Ð—Ð¼Ñ–Ð½Ð° ÐºÐ¾Ð»Ñ–Ñ€Ñƒ Ñ€Ð°Ð¼ÐºÐ¸ Ð¿Ñ€Ð¸ Ð½Ð°Ð²ÐµÐ´ÐµÐ½Ð½Ñ–
    boxShadow: state.isFocused ? '0 0 0 2px #8BAA36' : 'none', // Ð—Ð¼Ñ–Ð½Ð° Ñ‚Ñ–Ð½Ñ– Ñ€Ð°Ð¼ÐºÐ¸ Ð¿Ñ€Ð¸ Ð½Ð°Ð²ÐµÐ´ÐµÐ½Ð½Ñ–
    '&:hover': {
      borderColor: state.isFocused ? '#8BAA36' : '#CED4DA', // Ð—Ð¼Ñ–Ð½Ð° ÐºÐ¾Ð»Ñ–Ñ€Ñƒ Ñ€Ð°Ð¼ÐºÐ¸ Ð¿Ñ€Ð¸ Ð½Ð°Ð²ÐµÐ´ÐµÐ½Ð½Ñ–
    },
    '@include tablet': {
        width: 175,
        height: 41,
    },
    '@include desktop': {
        width: 192,
        height: 49,
    }
  }),
};

const SearchTypeSelector = () => {
  const [ setSelectedType] = useState('query');

  const handleTypeChange = (selectedOption) => {
    setSelectedType(selectedOption.searchType);
  };

  return (
    <div className={styles.cont_type_select}>
      <label htmlFor="select">Search by:</label>
      <ReactSelect
        className={styles.search_select}
        name="select"
        options={typesList}
        styles={customStyles}
        getOptionLabel={(option) => option.searchType}
        getOptionValue={(option) => option.searchType}
        isSearchable={false}
        onChange={handleTypeChange}
      />
    </div>
  );
};

export default SearchTypeSelector;
